import { actionType } from "../Actions/ActionsType"
import { combineReducers } from "redux"

const {
  REGISTRATION_SUCCESS,
  REGISTRATION_ERROR,
  LOGIN_ERROR,
  LOGIN_SUCCESS,
  TOKEN_FORM,
  LOGIN_USER,
} = actionType

const initialState = {
  email: "",
  isLogged: false,
  userData: {},
  registration: false,
  registrationMessage: "",
  regError: "",
  isError: false,
  logError: "",
  tripDetsOne: {},
  tripDetsTwo: {},
}

export const rootReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case REGISTRATION_SUCCESS:
      return { ...state, registrationMessage: payload, registration: true }
    case REGISTRATION_ERROR:
      return { ...state, regError: payload, isError: true }
    case LOGIN_ERROR:
      return { ...state, logError: payload, isError: true }
    case LOGIN_SUCCESS:
      //console.log(payload)
      return { ...state, userData: payload, isLogged: true }

    default:
      return state
  }
}
const reducers = combineReducers({
  rootReducer,
})

export default reducers
